import React  from 'react'
import "./Contact.css"
import  { useRef } from 'react';
import "./Contact.css";
import {MdOutlineEmail} from "react-icons/md";
import {RiMessengerLine} from "react-icons/ri"
import {BsWhatsapp} from "react-icons/bs";


const Contact = () => {

    const form = useRef();

  const clickHandler = () => {
    return alert("Message Successfully Sent");
  }
  
    
  return (
      
    <>
    
    
    
  <section id="contact">
  <div className="map-main">
		<iframe width="100%" height="300px" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="http://maps.google.com/maps?hl=en&amp;ie=UTF8&amp;ll=37.0625,-95.677068&amp;spn=56.506174,79.013672&amp;t=m&amp;z=4&amp;output=embed"></iframe>
	</div>
  <h2>Contact Us</h2>
  <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit.</p>
  <div className="container contact_container">
    <div className="contact_options">
      <article className='contact_option'>
        <MdOutlineEmail  className='contact-icon'/>
    <h4>Email</h4>
    <h5>Rapidops@mail.com</h5>
        <a href="mailto:Rapidops@mail.com" target="_blank">Send a Message</a>
      </article>
      <article className='contact_option'>
        <RiMessengerLine className='contact-icon'/>
        <h4>Messenger</h4>
        <h5>Rapidops</h5>
        <a href="https://m.me/play.Rapidops" target="_blank">Send a Message</a>
      </article>
      <article className='contact_option'>
        <BsWhatsapp className='contact-icon'/>
        <h4>Whatsapp</h4>
        <h5>+20 11 460 58 223</h5>
        <a href="https://api.whatsapp.com/send?phone=+201146058223" target="_blank">Send a Message</a>
      </article>
    </div>
    {/* End Of Contact Options */}
    <form ref={form} >
      <input type="text" name="name" placeholder='Your Full Name'  required/>
      <input type="email" name="email" placeholder='Your Email' required />
      <textarea name="message" placeholder='Your Message' rows="7" required></textarea>
      <button type='submit' className='btn btn-primary' onClick={() => clickHandler()}> Send Message</button>
    </form>
  </div>
</section>

  </>
    
    )
}

export default Contact